import { Button } from "react-bootstrap";
import FormModal from "../../../../components/Modal/FormModal";
import IMAGES from "../../../../assets/images";
import PaymentCheckout from "../PaymentCheckout";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { PATH } from "../../../../utils/path";

export default function SubscriptionPlanModal({ ...rest }) {
  const { show, handleClose, formData, singleSlotData } = rest;

  const [showPayment, setShowPayment] = useState(false);
  const navigate = useNavigate();

  return (
    <div className="AddSlot_modal">
      <FormModal
        className="modal-lg"
        handleClose={handleClose}
        show={show}
      >
        <div className="d-flex justify-content-center flex-column align-items-center text-center p-3">
          <img src={IMAGES.BUY_PLAN} alt="purchase plan" />
          <p className="fw-bold">You don&apos;t have any Subscription Plan</p>
          <p className="">
            We would love to serve you please proceed by clicking the button
            below and purchase your plan of your liking
          </p>
          <div className="d-flex justify-content-around gap-3">
            <Button
              className="block primary_bg"
              variant="primary"
              type="button"
              onClick={() => {
                handleClose();
                setShowPayment(true);
              }}
            >
              One-Time Payment
            </Button>
            <Button
              className="block primary_bg"
              variant="primary"
              type="button"
              onClick={() => navigate(PATH.PATIENT_PURCHASE_PLANS)}
            >
              Purchase Plan
            </Button>
          </div>
        </div>
      </FormModal>
      {showPayment && (
        <PaymentCheckout
          formData={formData}
          singleSlotData={singleSlotData}
          handleClose={() => setShowPayment(false)}
          show={showPayment}
        />
      )}
    </div>
  );
}
